import * as React from "react";

import styled from "react-emotion";
import Section from "./Section";
import memberData from "../lib/members.json";

const Category = ({
  header,
  members,
  className
}: {
  header: string;
  members: string[];
  className?: string;
}) => (
  <div className={className}>
    <h4>{header}</h4>
    <ul>
      {members.map(member => (
        <li>
          <a href={memberData.members[member]}>{member}</a>
        </li>
      ))}
    </ul>
  </div>
);

const StyledCategory = styled(Category)`
  h4 {
    color: #000d45;
    font-family: Aeonik;
    font-size: 28px;
    font-weight: bold;
    line-height: 34px;
  }
  li {
    color: #0734ff;
    font-family: Aeonik;
    font-size: 20px;
    line-height: 24px;
  }
`;

const Members = ({ className }: { className?: string }) => {
  const categories = Object.entries(memberData.categories).sort(
    (a, b) => (a < b ? -1 : 1)
  );
  return (
    <Section className={className}>
      <h2>Members</h2>
      {categories.map(([header, members]) => (
        <StyledCategory header={header} members={members as string[]} />
      ))}
    </Section>
  );
};

export default styled(Members)``;
